import React from "react";
import logo from "./logo.svg";
import "./App.css";
import { UuiContextProvider } from "./UuiContextProvider";
import { Button } from "@epam/loveship";
import { AnalyticsEvent, useUuiContext } from "@epam/uui-core";

function App() {
    const svc = useUuiContext();

    const clickAnalyticsEvent: AnalyticsEvent = {
        name: "clickAdd",
        category: "docs",
        label: "add_event"
    };

    const sendEventDirectly = () => {
        svc.uuiAnalytics.sendEvent({
            name: "clickEdit",
            category: "unknown",
            label: "edit_event"
        });
    };

    return (
        <UuiContextProvider>
            <div className="App">
                <Button caption="Add rule" clickAnalyticsEvent={clickAnalyticsEvent} />
                <Button caption="Edit rule" onClick={sendEventDirectly} />
                <Button caption="Remove rule" />
            </div>
        </UuiContextProvider>
    );
}

export default App;
