import { ContextProvider } from "@epam/uui";
import React from "react";

export const svc: any = {};

interface UuiContextProviderProps {
    children: React.ReactNode;
}

export const UuiContextProvider: React.FC<UuiContextProviderProps> = props => {
    return (
        <ContextProvider
            onInitCompleted={async context => {
                Object.assign(svc, context);
            }}
            gaCode="G-RN286K6JMW"
        >
            {props.children}
        </ContextProvider>
    );
};
